import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../../actions/';

import { Link } from 'react-router-dom';
import { MasterLayout } from '../../components/layouts';

class UserRegister extends Component {
    state = {
        form: {
            email: '',
            password: '',
            firstName: '',
            lastName: '',
        },
        errors: {
            email: null,
            password: null
        }
    }

    handleChangeInput = (e) => {
        const value = e.target.value;
        const name = e.target.name;

        this.setState({
            form: {
                ...this.state.form,
                [name]: value
            },
            errors: {
                email: null,
                password: null
            }
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();

        if (this.state.form.email.trim() === '') {
            return this.setState({
                errors: {
                    email: 'User name is required',
                    password: null
                }
            });
        }

        if (this.state.form.password.trim() === '') {
            return this.setState({
                errors: {
                    email: null,
                    password: 'Password is required'
                }
            });
        }

        this.props.actions.userRegister(this.state.form)
            .then(() => {
                this.props.history.push('/login');
            })
            .catch(err => {
                console.log(err);
            });
    }

    render() {
        return (
            <MasterLayout>
                <div className="content content-boxed overflow-hidden">
                    <div className="row">
                        <div className="col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4">
                            <div className="push-30-t push-20 animated fadeIn">
                                <div className="text-center">
                                    <i className="fa fa-2x fa-circle-o-notch text-primary"></i>
                                    <h1 className="h3 push-10-t">Register Page</h1>
                                </div>
                                <form className="form-horizontal push-50-t push-30" onSubmit={this.handleSubmit}>
                                    <div className="form-group">
                                        <div className="col-xs-12">
                                            <div className="form-material form-material-success">
                                                <input type="email"
                                                    className="form-control"
                                                    id="register-email"
                                                    name="email"
                                                    placeholder="User Email"
                                                    value={this.state.form.email}
                                                    onChange={this.handleChangeInput} />
                                                <label htmlFor="register-email">Email</label>
                                                {
                                                    this.state.errors.email &&
                                                    <div className="has-error">
                                                        <span className="help-block">{this.state.errors.email}</span>
                                                    </div>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <div className="col-xs-12">
                                            <div className="form-material form-material-success">
                                                <input type="password"
                                                    className="form-control"
                                                    id="register-password"
                                                    name="password"
                                                    placeholder="Password"
                                                    value={this.state.form.password}
                                                    onChange={this.handleChangeInput} />
                                                <label htmlFor="register-password">Password</label>
                                                {
                                                    this.state.errors.password &&
                                                    <div className="has-error">
                                                        <span className="help-block">{this.state.errors.password}</span>
                                                    </div>
                                                }
                                            </div>
                                        </div>
                                    </div>

                                    <div className="form-group">
                                        <div className="col-xs-12">
                                            <div className="form-material form-material-success">
                                                <input type="text"
                                                    className="form-control"
                                                    id="register-first-name"
                                                    name="firstName"
                                                    placeholder="First Name"
                                                    value={this.state.form.firstName}
                                                    onChange={this.handleChangeInput} />
                                                <label htmlFor="register-first-name">First Name</label>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="form-group">
                                        <div className="col-xs-12">
                                            <div className="form-material form-material-success">
                                                <input type="text"
                                                    className="form-control"
                                                    id="register-last-name"
                                                    name="lastName"
                                                    placeholder="Last Name"
                                                    value={this.state.form.lastName}
                                                    onChange={this.handleChangeInput} />
                                                <label htmlFor="register-last-name">Last Name</label>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="form-group">
                                        <div className="col-xs-12 col-sm-6 offset-sm-3">
                                            <button className="btn btn-sm btn-block btn-success" type="submit">Create Account</button>
                                        </div>
                                    </div>
                                </form>

                                <div className="text-center">
                                    <div className="push-10-t">I already have an account, </div>
                                    <Link to={"./login"}>
                                        <div> go to Login Page</div>
                                    </Link>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </MasterLayout>
        );
    }
}

const mapStateToProps = state => ({

});

const mapDispatchToProps = dispatch => ({
    actions: bindActionCreators(Actions, dispatch)
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(UserRegister);




