import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../../actions/';

import { Link } from 'react-router-dom'
import { MasterLayout } from '../../components/layouts';

class BookList extends Component {

	componentDidMount = () => {
		this.props.actions.getBookList();
	}

	deleteBook = (id) => {
		this.props.actions.deleteBook(id);
	}

	render() {
		const { bookList } = this.props;
		return (
			<MasterLayout>
				<h1>Books List page</h1>
				<div className="list-group">
					{
						bookList.map((item, key) =>
							<div className="list-group-item list-group-item-action" key={key}>
								<div>{item.title}</div>
								<div>{item.author}</div>
								<Link to={`book/${item.id}`}>
									<button className="btn btn-info">Detail</button>
								</Link>
								<Link to={`/book-edit/${item.id}`}>
									<button className="btn btn-primary">Edit</button>
								</Link>
								<button className="btn btn-danger" onClick={() => this.deleteBook(item.id)}>delete</button>
							</div>
						)
					}
				</div>
				<br />
				<Link to={`/book-create`}>
					<button className="btn btn-info">Create New Book</button>
				</Link>
			</MasterLayout>
		);
	}
}

const mapStateToProps = state => ({
	bookList: state.books.bookList || [],
});

const mapDispatchToProps = dispatch => ({
	actions: bindActionCreators(Actions, dispatch)
});

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(BookList);




