import {
    UserRegister,
    UserLogin,
    UserProfile,
    BooksList,
    BookDetails,
    BookCreation,
    BookEdition,
  } from '../containers/pages';
  
  import App from '../containers/App';
  
  const routes = [
    {
      component: App,
      routes: [
        {
            path: '/',
            root: true
        },
        {
          component: UserRegister,
          exact: true,
          path: '/register'
        },
        {
          component: UserLogin,
          path: '/login'
        },
        {
          component: UserProfile,
          path: '/profile',
          requireLogin: '/login',
        },
        {
          component: BooksList,
          path: '/books-list',
          requireLogin: '/login',
        },
        {
          component: BookDetails,
          path: '/book/:id',
          requireLogin: '/login',
        },
        {
          component: BookCreation,
          path: '/book-create',
          requireLogin: '/login',
        },
        {
          component: BookEdition,
          path: '/book-edit/:id',
          requireLogin: '/login',
        },
        {
          component: UserLogin,
          path: '/*',
          requireLogin: '/login',
        },
      ]
    }
  ];
  
  export default routes;
  


