import {
  CREATE_BOOK,
  GET_BOOK_LIST,
  GET_BOOK_LIST_SUCCESS,
  GET_BOOK_DETAIL,
  GET_BOOK_DETAIL_SUCCESS,
  UPDATE_BOOK_DETAIL,
  DELETE_BOOK,
  DELETE_BOOK_SUCCESS,
} from '../constants/ActionTypes';

let initialState = {
};

export default function books(state = initialState, action) {
  switch (action.type) {
    case CREATE_BOOK:
      return {
        ...state,
        createBook: action.payload.data || []
      }
    case GET_BOOK_LIST_SUCCESS:
      return {
        ...state,
        bookList: action.payload.data || []
      }
    case GET_BOOK_DETAIL:
      return {
        ...state,
        bookDetail: action.payload.data || []
      }
    case GET_BOOK_DETAIL_SUCCESS:
      return {
        ...state,
        bookDetail: action.payload.data || {}
      }
    case UPDATE_BOOK_DETAIL:
      return {
        ...state
      }
    case DELETE_BOOK_SUCCESS:
        console.log(action);
      const deleteId = action.meta.previousAction.params.id;
      const currentBooks = state.bookList;
      return {
        ...state,
        bookList: currentBooks.filter( item => item.id !== deleteId)
      }

      default:
  }

  return state;
};



