import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../../actions/';

import { MasterLayout } from '../../components/layouts';

class UserProfile extends Component {
	state = {
		onEditing: false,
		form: {
			first_name: this.props.userProfile.first_name || '',
			last_name: this.props.userProfile.last_name || '',
		}
	}

	toggleEditing = (value) => {
        let upComingValue = value !== undefined ? value : !this.state.onEditing;
		this.setState(
			{ onEditing: upComingValue }
		);
	}

	handleChangeInput = (e) => {
		const value = e.target.value;
		const name = e.target.name;

		this.setState({
			form: {
				...this.state.form,
				[name]: value
			}
		});
	}

	handleSubmit = (e) => {
		e.preventDefault();

        this.props.actions.updateUserProfile(this.state.form)
            .then( () => {
                this.toggleEditing(false);
            })
			.catch(err => {
				console.log(err);
			});
	}

	render() {
		const { userProfile } = this.props;
		return (
			<MasterLayout>
				<button className="btn btn-default" onClick={() => this.toggleEditing()}>Edit User Profile</button>

				{
					this.state.onEditing ?
						<form onSubmit={this.handleSubmit}>
							<div className="form-group">
								<label htmlFor="firstName">First Name</label>
								<input type="text"
									className="form-control"
									id="firstName"
									name="first_name"
									placeholder="First Name"
									value={this.state.form.first_name}
									onChange={this.handleChangeInput} />
							</div>

							<div className="form-group">
								<label htmlFor="lastName">Last Name</label>
								<input type="text"
									className="form-control"
									id="lastName"
									name="last_name"
									placeholder="Last Name"
									value={this.state.form.last_name}
									onChange={this.handleChangeInput} />
							</div>
							<button type="submit" className="btn btn-primary">Submit</button>
						</form>
						:
						<div>
							<div>
								Email: {userProfile.email}
							</div>
							<div>
								First name: {userProfile.first_name}
							</div>
							<div>
								Last name: {userProfile.last_name}
							</div>
						</div>
				}

			</MasterLayout>
		);
	}
}

const mapStateToProps = state => ({
	userProfile: state.auth.user,
});

const mapDispatchToProps = dispatch => ({
	actions: bindActionCreators(Actions, dispatch)
});

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(UserProfile);




